    //       height: $margin-auto;
    //       border-radius: calc(#{$padding * 2.5});
    //     }
    //   }
    //   li:nth-of-type(2) {
    //     ul {
    //       @include flex;
          // overflow-x: scroll;
          // scrollbar-width: none;
    //       li {
    //         img {
              // max-width: calc(#{$max-width * 8.15});
              // border-radius: calc(#{$padding * 2.5});
    //         }
    //       }
    //       li:not(:first-of-type) {
    //         padding-left: calc(#{$padding * 5});
    //         img {
    //           opacity: .5;
    //         }
    //       }
    //       li:not(:first-of-type):hover {
    //         img {
    //           opacity: 1;
    //         }
    //       }
    //     }
    //     ul::-webkit-scrollbar {
    //       display: none;
    //     }
    //   }
    // }
    // * {
    //   box-sizing: border-box;
    // }
    img {
      vertical-align: middle;
    }
    .container {
      position: relative;
      .mySlides {
        margin-bottom: 10px;
        img {
          max-width: 100%;
          border-radius: 10px;
        }
      }
      .row {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        overflow-x: scroll;
        .column:not(:last-of-type) {
          margin-right: 10px;
        }
        .column:first-of-type {
          img {
            opacity: 1;
          }
        }
        img {
          max-width: calc(#{$max-width * 8.15});
          border-radius: 5px;
          opacity: .5;
        }
        img:hover {
          opacity: 1;
        }
      }
      .row::-webkit-scrollbar {
        display: none;
    }
    }
    .mySlides {
      display: none;
    }
    .cursor {
      cursor: pointer;
    }
    .prev,
    .next {
      cursor: pointer;
      position: absolute;
      top: 40%;
      width: auto;
      padding: 16px;
      margin-top: -50px;
      color: white;
      font-weight: bold;
      font-size: 20px;
      border-radius: 0 3px 3px 0;
      user-select: none;
      -webkit-user-select: none;
    }
    .next {
      right: 0;
      border-radius: 3px 0 0 3px;
    }
    .prev:hover,
    .next:hover {
      background-color: rgba(0, 0, 0, 0.8);
    }
  }
}













